<template>
  <div id="detail">
    <detail-nav-bar
      @scrollToActiveSite="scrollToActiveSite"
      :tablist="['商品', '参数', '评论', '推荐']"
      ref="NavBar"></detail-nav-bar>
    <scroll :probe-type="3" :pull-up-load="true" ref="scroller" @bescroll="bescroll">
      <detail-swiper :topimges="topimges"></detail-swiper>
      <detail-goods-message :goods-message="goodsMessage"></detail-goods-message>
      <detail-shop-message :shop-message="shopMessage"></detail-shop-message>
      <detail-goods-info :goods-info="goodsInfo"></detail-goods-info>
      <detail-goods-size :goods-size="goodsSize" ref="goodsSize"></detail-goods-size>
      <detail-user-rate :user-rate="userRate" ref="userRate"></detail-user-rate>
      <div class="recommend">
        相关推荐
      </div>
      <goods-list :goods="recommends" ref="recommends"></goods-list>
    </scroll>
    <ScrollBack @click.native="backToTop" v-show="isbackshow"></ScrollBack>
    <detail-bottom-bar @addToCart="addToCart"></detail-bottom-bar>
  </div>
</template>

<script>
  //滚动
  import Scroll from "@/components/common/scroll/Scroll";
  //导航
  import DetailNavBar from "./childcopms/DetailNavBar";
  //轮播图
  import DetailSwiper from "./childcopms/DetailSwiper";
  //商品信息
  import DetailGoodsMessage from "./childcopms/DetailGoodsMessage";
  //店铺信息
  import DetailShopMessage from "./childcopms/DetailShopMessage";
  //商品细节
  import DetailGoodsInfo from "./childcopms/DetailGoodsInfo";
  //商品尺码
  import DetailGoodsSize from "./childcopms/DetailGoodsSize";
  //用户评价
  import DetailUserRate from "./childcopms/DetailUserRate";
  //商品推荐
  import goodsList from "@/components/content/goodlist/goodsList";
  //底部导航
  import DetailBottomBar from "./childcopms/DetailBottomBar";
  //导入混入
  import {BackTopMixin} from '@/common/mixin'
  //导入防抖函数
  import {debounce} from "@/common/utils";
  //network
  import {
    getDetailData,
    getDetailRecommend,
    GoodsMessage,
    ShopMessage,
    GoodsInfo,
    GoodsSize,
    UserRate
  } from "@/network/detail";

  export default {
    name: "Detail",
    components: {
      DetailNavBar,
      DetailSwiper,
      DetailGoodsMessage,
      DetailShopMessage,
      DetailGoodsInfo,
      GoodsSize,
      DetailGoodsSize,
      DetailUserRate,
      goodsList,
      DetailBottomBar,
      Scroll
    },
    // mixins:[imgLoadListerMixinByMounted],弃用
    mixins: [BackTopMixin],
    data() {
      return {
        iid: null,
        //幻灯片
        topimges: [],
        //商品信息
        goodsMessage: {},
        //  店铺信息
        shopMessage: {},
        // 商品细节
        goodsInfo: {},
        // 商品尺码
        goodsSize: {},
        // 用户评价
        userRate: {},
        //  商品推荐
        recommends: [],
        refresh: null,
        //保存导航标签的每个部分高度
        allScrollTops: [],
        //将getallScrollTops方法处理为防抖函数
        getallScrollTopsByTimer: null,
        //保存当前显示的部分
        currentIndex: 0,
      }
    },
    created() {
      //通过动态路由获取id
      this.iid = this.$route.params.iid
      console.log(this.iid);
      //根据iid获取商品数据
      getDetailData(this.iid).then(res => {
        // console.log(res);
        const result = res.result
        this.topimges = result.itemInfo.topImages
        this.goodsMessage = new GoodsMessage(result.itemInfo, result.columns, result.shopInfo.services)
        // console.log(this.goodsMessage);
        this.shopMessage = new ShopMessage(result.shopInfo)
        // console.log(this.shopMessage);
        this.goodsInfo = new GoodsInfo(result.detailInfo)
        // console.log(this.goodsInfo);
        // console.log(result.itemParams.info.images)
        this.goodsSize = new GoodsSize(result.itemParams)
        // console.log(this.goodsSize);
        //只有用用户评价时，才进入
        if (result.rate.cRate !== 0) {
          this.userRate = new UserRate(result.rate, result.rate.list[0])
          // console.log(this.userRate);
        }
      })
      //  获取推荐数据
      getDetailRecommend().then(res => {
        // console.log(res)
        this.recommends = res.data.list
        // console.log(this.recommends);
      })
    },
    mounted() {
      //开启Detail组件对imghasloaded事件的监听
      //防抖,减少refresh函数调用的次数
      this.refresh = debounce(this.$refs.scroller && this.$refs.scroller.refresh, 50)
      //创建获取每个部分高度的防抖函数
      this.getallScrollTopsByTimer = debounce(this.getallScrollTops, 50)
      //每加载完成一张图片就调用一次this.$refs.scroller.bscroll.refresh()以确保高度更新
      //并且调用一次getallScrollTopsByTimer()获取每个部分的距离顶部高度
      this.$bus.$on('imghasloaded', () => {
        // console.log('1')
        this.refresh()
        this.getallScrollTopsByTimer()
      })
    },
    destroyed() {
      //关闭Detail组件对imghasloaded事件的监听
      this.$bus.$off('imghasloaded')
    },
    methods: {
      scrollToActiveSite(index) {
        // console.log(index)
        this.$refs.scroller.scrollTo(0, -this.allScrollTops[index], 300)
      },
      /*
      * 获取所有主题的offsetTop
      * 问题：在哪里才能获取到正确的offsetTop
      * 1.created肯定不行，压根无法获取到对应元素
      * 2.mounted也不行，数据话没有获取到
      * 3.获取到数据在update（）生命周期函数中回调也不行，页面的DOM还未渲染完成
      * 4.$nextTick也不行，因为图片的高度还未计算在内
      * 5.在每次图片加载完成时，就进行计算，直到所有图片加载完成，所获取的高度一定正确
      * */
      getallScrollTops() {
        this.allScrollTops = []
        this.allScrollTops.push(0)
        this.allScrollTops.push(this.$refs.goodsSize.$el.offsetTop - 44)
        this.allScrollTops.push(this.$refs.userRate.$el.offsetTop - 44)
        this.allScrollTops.push(this.$refs.recommends.$el.offsetTop - 44)
        this.allScrollTops.push(Number.MAX_VALUE)
        // console.log(this.allScrollTops)
      },
      bescroll(position) {
        //是否显示回到顶部按钮
        this.IsBackTopshow(position)
        // console.log(position.y)
        //  方法一
        /*      for (let i = 0; i < this.allScrollTops.length; i++) {
                if (
                  (this.currentIndex !== i) && (
                    (i < this.allScrollTops.length - 1) && (-position.y >= this.allScrollTops[i] && -position.y < this.allScrollTops[i + 1])
                    ||
                    (i === this.allScrollTops.length - 1) && (-position.y >= this.allScrollTops[i])
                  )
                ) {
                  this.currentIndex = i
                  console.log(this.currentIndex)
                  this.$refs.NavBar.activeindex = this.currentIndex
                }
              }*/
        //  方法二 给allScrollTops添加一个极大值
        for (let i = 0; i < this.allScrollTops.length - 1; i++) {
          if ((this.currentIndex !== i) && (-position.y >= this.allScrollTops[i] && -position.y < this.allScrollTops[i + 1])) {
            this.currentIndex = i
            // console.log(this.currentIndex)
            this.$refs.NavBar.activeindex = this.currentIndex
          }
        }
      },
      //  加入购物车
      addToCart() {
        let product = {}
        product.iid = this.iid
        product.image = this.topimges[0]
        product.title = this.goodsMessage.title
        product.desc = this.goodsMessage.desc
        product.realPrice = this.goodsMessage.realPrice
        product.count = 1
        product.checked = true
        // console.log(product)
        this.$store.dispatch('addProduct', product).then(res => {
          this.$toast.changeShow(res, 2000)
        })
      }
    }
  }
</script>

<style scoped>
  #detail {
    /*padding-top: 44px;*/
    height: 100vh;
    position: relative;
    z-index: 999;
    background-color: white;
  }

  .wrapper {
    height: calc(100% - 44px - 49px);
  }

  .recommend {
    width: 90%;
    background-color: var(--color-high-text);
    text-align: center;
    font-size: 25px;
    border-radius: 10px;
    margin: 5px auto;
    color: white;
  }
</style>
