<template>
  <div class="topnav">
    <top-nav>
      <div slot="left" class="left" @click="BackToHome">
        <img src="@/assets/img/common/back.svg" alt="">
      </div>
      <div slot="center" class="center">
        <div v-for="(item,index) in tablist"
             class="center-item"
             @click="changeActiveItem(index)"
             :class="{active: index === activeindex}">{{item}}</div>
      </div>
    </top-nav>
  </div>
</template>

<script>
  import topNav from "@/components/common/topnav/topNav";
  export default {
    name: "DeTailNavBar",
    components: {
      topNav
    },
    data() {
      return {
        // tablist: ['商品', '参数', '评论', '推荐'],
        activeindex: 0
      }
    },
    props:{
      tablist:{
        type:Array,
        default(){
          return []
        }
      },
    },
    methods:{
      changeActiveItem(index){
        this.activeindex = index
        this.$emit('scrollToActiveSite',index)
      },
      BackToHome(){
        this.$router.back()
      }
    }
  }
</script>

<style scoped>
  .active{
    color: var(--color-high-text);
  }
  .left{
    padding-top: 5px;
    height: 100%;
  }

  .center {
    display: flex;
  }

  .center-item {
    flex: auto;
  }
</style>
