import {require1} from "./require"
export function getDetailData(iid) {
  return require1({
    url:'/detail',
    params:{
      iid
    }
  })
}
export function getDetailRecommend() {
  return require1({
    url:'/recommend'
  })

}
//商品信息类
export class GoodsMessage{
  constructor(itemInfo, columns, services) {
    this.title = itemInfo.title
    this.desc = itemInfo.desc
    this.newPrice = itemInfo.price
    this.oldPrice = itemInfo.oldPrice
    this.discount = itemInfo.discountDesc
    this.realPrice = itemInfo.lowNowPrice
    this.columns = columns
    this.services = services
  }
}
//店铺信息类
export class ShopMessage{
  constructor(shopInfo) {
    this.logo = shopInfo.shopLogo
    this.name = shopInfo.name
    this.fans = shopInfo.cFans
    this.sells = shopInfo.cSells
    this.score = shopInfo.score
    this.goodsCount = shopInfo.cGoods
  }
}
//商品细节类
export class GoodsInfo {
  constructor(detailInfo) {
    this.desc = detailInfo.desc
    this.key = detailInfo.detailImage[0].key
    this.imgList = detailInfo.detailImage[0].list
  }
}
//商品尺码类
export class GoodsSize {
  constructor(itemParams) {
  if (itemParams.info.images){
    this.image = itemParams.info.images
  }else {
    this.image = []
  }
    this.infokey = itemParams.info.key
    this.infoset = itemParams.info.set
    this.rulekey = itemParams.rule.key
    this.ruledesc = itemParams.rule.desc
    this.ruledisclaimer = itemParams.rule.disclaimer
    this.ruletables = itemParams.rule.tables
  }
}
//用户评价
export class UserRate {
  constructor(rate,ratelist) {
    this.cRate = rate.cRate
    this.content = ratelist.content
    this.time = ratelist.created
    this.extraInfo = ratelist.extraInfo
    this.goodsImages = ratelist.images
    this.rateId = ratelist.rateId//订单号
    this.goodsStyle =  ratelist.style
    this.userAvatar = ratelist.user.avatar
    this.uerName = ratelist.user.uname
  }

}
