<template>
  <div id="detailgoodsinfo">
    <div class="desc">
      <span>{{goodsInfo.desc}}</span>
    </div>
    <div class="key">
      {{goodsInfo.key}}
    </div>
    <div class="imglist">
      <img v-for="item in goodsInfo.imgList" :src="item" alt="" @load="imgLoaded">
    </div>
  </div>
</template>

<script>
  export default {
    name: "DetailShopInfo",
    data(){
      return{
        imgcount:0,
        imgListLength:0
      }
    },
    props: {
      goodsInfo: {
        type: Object,
        default() {
          return {}
        }
      }
    },
    methods:{
/*      imgLoaded(){
        //防抖，只有加载到最后一张图片时才向外发送事件，页面高度才重新开始计算
        this.imgcount++
        if(this.imgcount === this.imgListLength - 1){
          this.$emit('imghasloaded')
          // console.log("1");
        }
      }*/
      imgLoaded(){
        this.$bus.$emit('imghasloaded')
      }
    },
    watch:{
      //监听goodsInfo属性的变化，一旦该属性变化就修改imgListLength的值,
      //避免该值在服务器未响应时为0
      goodsInfo(){
        this.imgListLength = this.goodsInfo.imgList.length
        // console.log("2");
      }
    }
  }
</script>

<style scoped>
  .desc {
    padding: 0 10px;
    border-bottom: 1px solid rgba(0, 0, 0, .3);
    line-height: 25px;
  }

  .key {
    padding: 0 10px;
    text-align: center;
    color: var(--color-high-text);
    font-size: 20px;
    line-height: 40px;
  }

